.title{
    color: #fff;
    text-transform: uppercase;
    line-height: 160%;
    font-weight: 300;
    font-size: 1.5vh;
    position: absolute;
    left: 17%;
    margin-top: 21%;
    margin-bottom: 5%;
}
.title-content{
    display: block; /*Makes each line of text inside of the span 100% width so it will fall on the next line.*/
    opacity: 0;
    animation: revealText .5s forwards; /*Adding the animation over .5 seconds. Forwards means the last state will be the styles applied at 100% (to).*/
}
/*Adding individual animation delays so that the text animates in at different stages.*/
.title-second{
    animation-delay: .175s;
}
.title-third{
    animation-delay: .25s;
}
.title-fourth{
    animation-delay: .325s;
}
/*From and to is just another way of saying start at 0 and end at 100%.*/
@keyframes revealText {
    from{
        transform: translateY(20px); /*Moves the element down by 20px.*/
    }
    to{
        opacity: 1; /*Turn the opacity back on so that we can see the element in it's final animation state.*/
        transform: none; /*Removes the 20px so that the text will be at the normal starting position at the end of the animation.*/
    }
}
.card{
    max-width: 300px;
    width: 50%;
    height: 300px;
    max-height: 100%;
    margin-top: 5%;
    background-color: #333;
    border-radius: 10px;
    outline: 1px solid 
    rgba(255, 255, 255, .5); /* The outline works the same way as a border with added perks. */
    outline-offset: -8px; /* Negative offset to bring in the outline border. */
    box-shadow: 3px 3px 10px
    rgba(0, 0, 0, .1);
    position: relative; /* Relative position because we are using position absolute on elements inside the card.
    This will make sure the said elements do not float outside of the card. Important for the zoom effect.*/
    overflow: hidden; /* Since we are scaling the image on hover, we have to hide the overflow. Important for the zoom effect.*/
}
.card-image{
    /* All the following styles are important for the zoom effect. */
    position: absolute;
    inset: 0; /* Fixes the starting points of the image to be on all 4 corners. */
    width: 100%;
    height: 100%;
    object-fit: cover; /* Makes the image maintain its aspect ratio while filling the up entire card. */
    opacity: .6; /* Making the image slightly transparent so that it blends with the card background. */
    transition: transform .75s; /* Adds a transition over .75 seconds on the transform property (the scaling). */
}
.card:hover .card-image{
    transform: scale(1.15); /* When the card is hovered, the image inside of it is scaled up. Important for the zoom effect. */
}
/* Not important for the zoom effect. */
.card-title{
    position: absolute;
    color: #fff;
    left: 15px;
    bottom: 15px;
    font-size: 20px;
    text-shadow: 0 0 1px 
    rgba(0, 0, 0, .5);
}


